.footer {
  padding: 1.5625rem;
  margin: 0.46875rem auto;
  border-top: 0.46875rem solid #d2d2d2;
}
.footer span.wechat {
  display: inline-block;
  width: 30%;
  text-align: center;
}
.footer span.wechat img {
  width: 2.5rem;
  display: inline;
}
.footer .flex-column {
  padding: 0;
  margin: 0;
}
.footer .flex-column li {
  padding-top: 0.15625rem;
  padding-bottom: 0.15625rem;
  margin: 0;
  line-height: 1.5rem;
}
.footer span.youtube {
  display: inline-block;
  width: 30%;
  text-align: center;
}
.footer span.youtube img {
  width: 2.5rem;
  display: inline;
}
.footer span.facebook {
  display: inline-block;
  width: 30%;
  text-align: center;
}
.footer span.facebook img {
  width: 2.5rem;
  display: inline;
}
.footer h3.title {
  line-height: 1.5rem;
  font-size: 1.125rem;
  padding-top: 0.625rem;
}
.footer .list {
  padding: 0;
  margin: 0;
}
.footer .list a {
  padding: 0.15625rem 0.15625rem 0.15625rem 0;
  color: #333;
}
.footer .list a:hover {
  color: #F6912D !important;
  text-decoration: underline !important;
}
.footer .nav a {
  padding: 0.15625rem 0.15625rem 0.15625rem 0;
  color: #333;
}
.footer .nav a:hover {
  color: #F6912D !important;
  text-decoration: underline !important;
}
.footer li.wrap-box {
  padding-top: 0.625rem;
}
